import React from 'react'
import { Container, Row, Column, Heading, Spacer, Text, ThemeProvider } from 'axis'
// import 'axis/dist/index.css'

const App = () => (
  <ThemeProvider>
    <Container>
      <Heading>Layout</Heading>
      <Spacer />
      <Text>Layout in axis is fully responsive, it built around grid system.</Text>
      <Spacer />
      <Text>This will take 2/3 and 1/3 space for each column respectively. But, if the screen size if small then the next columns are pushed into new row.</Text>
      <Spacer height={15} />
      <Row>
        <Column md={2} sm={2}>
          <div style={{ background: 'red', color: 'white', padding: 10 }}><Text>I'm taking 2/3 of available space</Text></div>
        </Column>
        <Column md={1} sm={2}>
          <div style={{ background: 'green', color: 'white', padding: 10 }}><Text>I'm taking 1/3 of available space</Text></div>
        </Column>
      </Row>
    </Container>
  </ThemeProvider>
)

export default App
